.scroll-top {
  position: fixed;
  right: 40px;
  bottom: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: #1a77b5;
  border: 1px solid #fff;
  padding-bottom: 2px;
  transition: all 0.3s ease;
  z-index: 11;
}
.scroll-top path {
  transition: all 0.3s ease;
}
.scroll-top:hover {
  background: #fff;
}
.scroll-top.hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

@media (max-width: 768px) {
  .scroll-top {
    right: 20px;
    bottom: 20px;
    width: 40px;
    height: 40px;
  }
}
/* popups */
.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  transition: opacity 0.4s ease;
  overflow-y: auto;
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  position: relative;
  width: 100%;
  position: relative;
  transition: transform 0.3s linear;
  transform: rotateY(90deg);
}

.popup.open .popup__content {
  transform: rotateY(0);
}

.popup input {
  width: 100%;
}

.close-popup {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
  width: 40px;
  height: 40px;
  transition: transform 0.4s linear;
}

.close-popup img {
  height: 100%;
}

.close-popup:hover {
  transform: rotate(180deg);
}

.popup__search_form {
  position: relative;
}
.popup__search_input {
  background: rgba(234, 237, 242, 0.8);
}
.popup__search_input:active,
.popup__search_input:focus {
  background: rgba(234, 237, 242, 0.8);
}
.popup__search_img {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
}

.popup__search::-webkit-scrollbar {
  width: 4px;
  background-color: #edf2f5;
  padding: 5px;
}
.popup__search::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #1a77b5;
}

.popup__search-result {
  margin-top: 0;
  max-height: 370px;
  overflow-y: auto;
}
.popup__search-result::-webkit-scrollbar {
  width: 4px;
  background-color: #edf2f5;
  padding: 5px;
}
.popup__search-result::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #1a77b5;
}

.no-results-message {
  opacity: 0.6;
}

.none {
  display: none;
}

.popup {
