  flex-direction: column;
  justify-content: space-between;
  gap: 80px;
  position: relative;
  z-index: 2;
}
.popup-change__text {
  color: #fff;
}
.popup-change__title {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 30px;
  color: #2e2e2e;
  margin-bottom: 20px;
}
.popup-change__subtitle {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #2e2e2e;
  margin-bottom: 30px;
}
.popup-change__form {
  display: flex;
  flex-wrap: wrap;
  gap: 16px 12px;
}
.popup-change__form input,
.popup-change__form button {
  flex: 1 0 40%;
}
.popup-change__politic {
  width: 100%;
}
.popup-change__img {
  position: absolute;
  bottom: 0;
  left: 60%;
  max-width: 45%;
}
.popup-change__img img {
  display: block;
  width: 100%;
  height: 100%;
