    border-right: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0px 20px 0px 0px;
    height: 60px;
    display: flex;
  }
}

.header__nav-name,
.header__nav-link {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  color: #2e2e2e;
  transition: color 0.3s ease-in-out;
}

.header__nav-name:hover,
.header__nav-link:hover {
  color: #5c4099;
}

.header__nav-item--disc {
  list-style: disc;
}

.header__nav-item--descp {
  padding-left: 270px;
}

@media (width < 1300px) {
  .header__nav-item--descp {
    padding-left: 100px;
  }
}

@media (width < 1250px) {
  .header__top-item--btns {
    display: none;
  }
}

@media (width < 1200px) {
  .header__nav-item--descp {
    padding-left: 0px;
  }
}

@media (max-width: 1024px) {
  .header__top {
    padding: 12px 0;
    border-bottom: 1px solid #dfdff2;
  }
}

.header__info-btns {
  display: none;
  background: #3a3145;
  padding: 20px 10px;
  margin-right: 10px;
  margin-left: 10px;
}

@media (max-width: 1024px) {
  .header__info-btns {
    display: flex;
  }

  .header {
    background: #fff;
    position: sticky;
    top: 0;
  }

  .header__nav-item--services .header__nav-open {
    padding: 18px 0px;
  }

  .header__nav-item {
    padding: 0 10px;
  }

  .header.active {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }

  .header__plashka {
    padding: 7px 10px;
    font-size: 11px;
  }

  .header__logo {
    max-width: 144px;
  }

  .header__top {
    padding: 12px 0;
  }

  .header__mobile {
    display: block;
  }

  .header__eye {
    display: none;
  }

  .header__infos {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .header__info {
    align-items: flex-start;
    font-size: 18px;
    flex-direction: column;
    gap: 0px;
  }

  .header__info.header__top-item {
    display: none;
  }

  .header__info span,
  .header__info button {
    font-size: 13px;
    line-height: 100%;
  }

  .header__bottom {
    padding: 24px 0;
    border-bottom: none;
    background: #fff;
    position: absolute;
    top: 100%;
    left: -1024px;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    height: calc(100vh - 85px);
    overflow-y: auto;
    transition: all 0.3s ease;
  }

  .header__bottom.active {
    display: block;
    left: 0;
    opacity: 1;
    pointer-events: all;
  }

  .header__bottom::-webkit-scrollbar {
    width: 3px;
  }

  .header__bottom::-webkit-scrollbar-thumb {
    background: #1a77b5;
    width: 3px;
  }

  .header__bottom::-webkit-scrollbar-track {
    background: #edf2f5;
    width: 3px;
  }

  .header__bottom-row {
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    gap: 20px;
  }

  .header__nav-list {
    flex-direction: column;
    align-items: stretch;
    gap: 16px;
  }

  .header__nav-link {
    display: block;
    width: 100%;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    padding-bottom: 16px;
    border-bottom: 1px solid #d9d9d9;
  }

  .header__nav-open {
    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 16px;
    gap: 0 8px;
  }

  .header__nav-item--services {
    background: #f0edf8;
    padding: 0;
  }

  .header__nav-open.active .header__nav-sublist {
    opacity: 1;
    pointer-events: all;
    position: static;
    background: white;
    z-index: 10;
  }

  .header__nav-name,
  .header__nav-link {
    font-size: 16px;
    text-align: left;
  }

  .header__nav-item--services .header__nav-burger {
    margin-left: 10px;
  }

  .header__nav-item--descp,
  .header__nav-item--services {
    border-bottom: 1px solid #d9d9d9;
  }

  .header__nav-sublist {
    min-width: 100%;
    opacity: 1;
    pointer-events: all;
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease;
  }

  .header__nav-sublist > li {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .header__nav-sublist > li > a {
    width: -moz-fit-content;
    width: fit-content;
    max-width: calc(100% - 30px);
    font-size: 16px;
  }

  .header__nav-sublist > li > a.active {
    color: #2e2e2e;
  }

  .header__nav-arrow {
    display: flex;
    width: 30px;
    align-items: center;
    justify-content: flex-end;
    flex-grow: 1;
  }

  .header__nav-arrow svg {
    transition: all 0.3s ease;
  }

  .header__nav-arrow.active svg {
    transform: rotate(180deg);
  }

  .header__nav-subsublist {
    min-width: 100%;
    opacity: 1;
    pointer-events: all;
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease;
  }

  .header__nav-subsublist ul {
    padding-right: 0;
    padding-left: 30px;
    max-height: none;
    padding-top: 10px;
  }

  .header__nav-subsublist ul a {
    padding: 4px 0;
    font-size: 16px;
  }

  .header__btns button {
    flex-grow: 1;
  }

  .header {
    margin-bottom: 0px;
    padding-bottom: 0px;
  }

  .header__bottom.active .header__bottom-row {
    padding-top: 20px;
  }
}

.header__nav-open.active {
  background-color: white;
}

.header__info-email {
  display: none;
}
.social-networks--mob {
  display: none;
}
@media (width < 1024px) {
  .header__info-email {
    display: flex;
  }

  .header__info-email p,
  .header__info-email a {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    color: #2e2e2e;
  }

  .header__bottom.active {
    padding-top: 0;
  }

  .header__btns {
    display: none;
  }

  .header__nav-open.active .header__nav-sublist {
    padding-top: 20px;
  }

  .social-networks--mob {
    display: flex;
    padding-left: 10px;
  }

  .header__infos {
    padding-left: 10px;
  }

  .header__nav-arrow svg {
    transform: rotate(0deg);
  }

  .header__info-title {
    font-size: 13px;
    line-height: 114%;
  }
}
.popup input {
  flex: 0 0 100%;
  border: 1px solid #dbe1dd;
  padding: 18px;
  width: 320px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  color: #2e2e2e;
  height: 58px;
}

.form__btns {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.form__btns div:last-child {
  display: none;
}

.form__btns button {
  width: 321px;
  flex-shrink: 0;
}

.popup-change__close {
  width: 24px;
  height: 24px;
  background: #5c4099;
  padding: 5px;
}

.popup__content {
  z-index: 10;
}

.form__bottom-text {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #2e2e2e;
  margin-bottom: 10px;
}

.form__bottom-phones {
  border-left: 2px solid #5c4099;
}

.form__bottom-phones a {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 25px;
  color: #2e2e2e;
  padding-left: 10px;
}

.form__bottom-phones p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #2e2e2e;
  padding-left: 10px;
}

.popup__content-picture {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 0;
}

.popup-change__title,
.popup-change__subtitle,
.popup input,
.politic {
  z-index: 10;
  position: relative;
}

.popup input {
  width: 100%;
}

@media (width < 1024px) {
  .popup-change__title {
    font-size: 25px;
    margin-bottom: 30px;
  }

  .popup-change__subtitle {
    font-size: 15px;
  }
}

@media (width < 700px) {
  .form__btns {
    flex-direction: column-reverse;
  }

  .popup-change__content {
    padding: 20px;
  }

  .form__btns button {
    width: 100%;
  }

  .form__bottom-phones a {
    font-size: 22px;
  }
}
.error {
  position: relative;
  padding: 200px 0;
}

.error__main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 1px;
  position: relative;
  z-index: 10;
}

.error__picture {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  opacity: 0.9;
}

.error__title {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 250px;
  color: #2e2e2e;
  line-height: 110%;
}

.error__subtitle {
  display: block;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 25px;
  text-align: center;
  color: #2e2e2e;
}

.error__text {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #2e2e2e;
  margin-bottom: 70px;
}

@media (width < 1024px) {
  .error {
    padding: 100px 0 140px;
  }

  .error__picture {
    display: none;
  }

  .error__title {
    font-size: 125px;
  }

  .error__subtitle {
    font-size: 22px;
    margin-bottom: 10px;
  }

  .error__text {
    font-size: 15px;
    margin-bottom: 50px;
    text-align: center;
  }
}

.header__nav-open.active .header__nav-burger span:nth-child(1) {
  transform: translateY(6px) rotate(45deg);
}

.header__nav-open.active .header__nav-burger span:nth-child(2) {
  opacity: 0;
}

.header__nav-open.active .header__nav-burger span:nth-child(3) {
  transform: translateY(-6px) rotate(-45deg);
}
.burger {
  width: 24px;
  height: 24px;
  background-color: #5c4099;
  display: flex;
  gap: 3px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.burger span {
  width: 16px;
  height: 1px;
  background-color: white;
  display: block;
  transition: transform 0.4s ease, opacity 0.4s ease;
}

/* ---  :    --- */

.burger.active span:nth-child(1) {
  transform: translateY(4px) rotate(45deg);
}

.burger.active span:nth-child(2) {
  opacity: 0;
}

.burger.active span:nth-child(3) {
  transform: translateY(-4px) rotate(-45deg);
}
